import { all, fork } from "redux-saga/effects";

import applicationSaga from "../sagas/application";
import standardTokenSaga from "../sagas/standardToken";
import standardTokenStandardCrowdsaleSaga from "../sagas/standardTokenStandardCrowdsale";

export default function* rootSaga() {
  yield all([
    fork(applicationSaga),
    fork(standardTokenSaga),
    fork(standardTokenStandardCrowdsaleSaga)
  ]);
}



