import React from "react";
import PropTypes from "prop-types";

const renderButton = (onAction, actionText) => {
  if (!actionText) return null;

  return (
    <div className="mv3 dib v-btm dim pointer">
      <button
        onClick={onAction}
        className="f5 ba link bw1 ph3 pv2 mb2 black bg-transparent b--black pointer"
      >
        {actionText}
      </button>
    </div>
  );
};

const renderError = () => (
  <div className="mv3 dib v-btm ba bw1 pa3 w-80">
    This contract is not ready for deployment on this network. Please change to
    another network to deploy this contract.
  </div>
);

const MainLayout = ({ title, content, actionText, onAction, error }) => (
  <div>
    <div className="f1">{title}</div>
    <div className="mw6">{content}</div>
    {error ? renderError() : renderButton(onAction, actionText)}
  </div>
);

MainLayout.propTypes = {
  title: PropTypes.string,
  content: PropTypes.object,
  actionText: PropTypes.string,
  onAction: PropTypes.func,
  error: PropTypes.bool
};

export default MainLayout;



