import { all, takeEvery, put, select } from "redux-saga/effects";
import { getNetwork, types } from "../reducers/application";
import getWeb3 from "../services/web3/getWeb3";
import getAccounts from "../services/web3/getAccounts";

function* getSelectedWeb3() {
  const network = yield select(getNetwork);
  const web3 = yield getWeb3(network);
  return web3;
}

export function* updateNetworkId() {
  try {
    const web3 = yield getSelectedWeb3();
    const networkId = yield web3.eth.net.getId();

    let networkIdVerbose;

    switch (networkId) {
      case 1:
        networkIdVerbose = "Main";
        break;
      case 2:
        networkIdVerbose = "Morden";
        break;
      case 3:
        networkIdVerbose = "Ropsten";
        break;
      case 4:
        networkIdVerbose = "Rinkeby";
        break;
      case 42:
        networkIdVerbose = "Kovan";
        break;
      default:
        networkIdVerbose = `Custom Network: ${networkId}`;
    }

    yield put({
      type: types.UPDATE_NETWORK_ID_SUCCESS,
      payload: {
        networkId,
        networkIdVerbose
      }
    });
  } catch (e) {
    yield put({
      type: types.UPDATE_NETWORK_ID_FAILURE,
      payload: e
    });
  }
}

export function* loadWalletAddress() {
  try {
    const web3 = yield getSelectedWeb3();
    const accounts = yield getAccounts(web3);

    if (!accounts || !accounts.length || accounts.length === 0)
      throw new Error("Accounts not found");

    yield put({
      type: types.LOAD_WALLET_ADDRESS_SUCCESS,
      payload: accounts[0]
    });
  } catch (e) {
    yield put({
      type: types.LOAD_WALLET_ADDRESS_FAILURE,
      payload: e.message
    });
  }
}

export default function* rootApplicationSaga() {
  yield all([
    takeEvery(types.LOAD_WALLET_ADDRESS, loadWalletAddress),
    takeEvery(types.UPDATE_NETWORK_ID, updateNetworkId)
  ]);
}



