import _ from "lodash";
import { all, takeEvery, put, select } from "redux-saga/effects";
import { types } from "../reducers/standardToken";
import { getNetwork } from "../reducers/application";

import getWeb3 from "../services/web3/getWeb3";
import StandardTokenFactory from "../services/contracts/StandardTokenFactory.json";

// TODO do a better estimate
const DEFAULT_GAS = 2000000;

function* getSelectedWeb3() {
  const network = yield select(getNetwork);
  const web3 = yield getWeb3(network);
  return web3;
}

function txHashWrapper(tx) {
  return new Promise((resolve, reject) => {
    tx.on("transactionHash", resolve);
    tx.on("error", reject);
  });
}

export function* mintToken({ payload }) {
  try {
    const {
      factoryAddress,
      fromAddress,
      tokenName,
      tokenSymbol,
      tokenDecimal,
      tokenSupply
    } = payload;

    const web3 = yield getSelectedWeb3();

    const { abi } = StandardTokenFactory;
    const contract = new web3.eth.Contract(abi, factoryAddress, {
      from: fromAddress
    });

    const gasPrice = yield web3.eth.getGasPrice();
    const mintTokenTransaction = contract.methods.mintAndForwardToken(
      tokenName,
      tokenSymbol,
      tokenDecimal,
      tokenSupply,
      fromAddress
    );

    const unresolvedTx = mintTokenTransaction.send({
      from: fromAddress,
      gas: DEFAULT_GAS,
      value: web3.utils.toWei("0.05", "ether"),
      gasPrice
    });

    const txHash = yield txHashWrapper(unresolvedTx);

    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_TX,
      payload: txHash
    });

    const txReceipt = yield unresolvedTx;

    const deployedTokenAddress =
      _.get(txReceipt, "events.TokenMinted.returnValues.tokenAddress") ||
      _.get(txReceipt, "events.TokenMinted.returnValues.1") ||
      null;

    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_SUCCESS,
      payload: deployedTokenAddress
    });
  } catch (e) {
    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_FAILURE,
      payload: e.message
    });
  }
}

export default function* rootStandardTokenSaga() {
  yield all([takeEvery(types.EXECUTE_STANDARD_TOKEN, mintToken)]);
}



