import { types as Web3Types } from "../services/web3/getWeb3";

export const initialState = {
  network: Web3Types.INJECTED,
  networkId: null,
  networkIdVerbose: "",
  customRpc: "",
  walletAddress: "",
  walletAddressError: ""
};

// Actions
export const types = {
  UPDATE_WEB3: "UPDATE_WEB3",

  UPDATE_NETWORK_ID: "UPDATE_NETWORK_ID",
  UPDATE_NETWORK_ID_SUCCESS: "UPDATE_NETWORK_ID_SUCCESS",
  UPDATE_NETWORK_ID_FAILURE: "UPDATE_NETWORK_ID_FAILURE",

  LOAD_WALLET_ADDRESS: "LOAD_WALLET_ADDRESS",
  LOAD_WALLET_ADDRESS_SUCCESS: "LOAD_WALLET_ADDRESS_SUCCESS",
  LOAD_WALLET_ADDRESS_FAILURE: "LOAD_WALLET_ADDRESS_FAILURE"
};

// Reducers
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case types.UPDATE_WEB3:
      return {
        ...state,
        network: action.payload.network,
        customRpc: action.payload.customRpc
      };
    case types.UPDATE_NETWORK_ID:
      return {
        ...state,
        networkId: null,
        networkIdVerbose: ""
      };
    case types.UPDATE_NETWORK_ID_SUCCESS:
      return {
        ...state,
        networkId: action.payload.networkId,
        networkIdVerbose: action.payload.networkIdVerbose
      };
    case types.UPDATE_NETWORK_ID_FAILURE:
      return {
        ...state,
        networkId: null,
        networkIdVerbose: ""
      };
    case types.LOAD_WALLET_ADDRESS_SUCCESS:
      return {
        ...state,
        walletAddress: action.payload,
        walletAddressError: ""
      };
    case types.LOAD_WALLET_ADDRESS_FAILURE:
      return {
        ...state,
        walletAddress: "",
        walletAddressError: action.payload
      };
    default:
      return state;
  }
}

// Action Creators
export function updateWeb3(payload) {
  return {
    type: types.UPDATE_WEB3,
    payload
  };
}

export function updateNetworkId() {
  return {
    type: types.UPDATE_NETWORK_ID
  };
}

export function loadWalletAddress() {
  return {
    type: types.LOAD_WALLET_ADDRESS
  };
}

// Selectors
export function getNetwork(store) {
  return store.application.network;
}

export function getCustomRpc(store) {
  return store.application.customRpc;
}

export function getNetworkId(store) {
  return store.application.networkId;
}

export function getNetworkIdVerbose(store) {
  return store.application.networkIdVerbose;
}

export function getWalletAddress(store) {
  return store.application.walletAddress;
}



