import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import _ from "lodash";

import {
  getNetworkId,
  getNetworkIdVerbose,
  loadWalletAddress,
  getWalletAddress
} from "../reducers/application";

import {
  displays as displayTypes,
  contractExecute,
  displayForm,
  getDisplay,
  getTxHash,
  getTokenAddress,
  getCrowdsaleAddress,
  getExecuting,
  getExecutingError,
  getExecuteParams
} from "../reducers/standardTokenStandardCrowdsale";
import ContractAddresses from "../services/contracts/ContractAddresses";

import MainLayout from "./MainLayout";
import StatusLayout from "./StatusLayout";
import StandardTokenStandardCrowdsaleFormLayout from "./StandardTokenStandardCrowdsaleFormLayout";

const CONTRACT_NAME = "STANDARD_TOKEN_STANDARD_CROWDSALE";

class StandardTokenStandardCrowdsaleContainer extends Component {
  constructor(props) {
    super(props);

    this.renderStatusLayout = this.renderStatusLayout.bind(this);
    this.handleContractExecute = this.handleContractExecute.bind(this);
    this.renderMainLayout = this.renderMainLayout.bind(this);
  }

  componentWillMount() {
    this.props.loadWalletAddress();
  }

  getContractAddress() {
    return _.get(
      ContractAddresses,
      `${this.props.networkIdVerbose}.${CONTRACT_NAME}`
    );
  }

  handleContractExecute({
    tokenName,
    tokenSymbol,
    tokenDecimal,
    tokenSupply,
    price
  }) {
    this.props.contractExecute({
      factoryAddress: this.getContractAddress(),
      fromAddress: this.props.walletAddress,
      tokenName,
      tokenSymbol,
      tokenDecimal,
      tokenSupply,
      price
    });
  }

  renderMainLayout() {
    const text = (
      <div>
        <p>Hassle free deployment of both Token and Crowdsale contract.</p>
        <p>
          This contract deploys a standard ERC20 token and sends the tokens to a
          crowdsale contract where anyone can purchase the token from, at a
          fixed price.
        </p>
        <p>
          Ethers collected from the crowdsale will be transfer to the executing
          account automatically.
        </p>
      </div>
    );
    return (
      <MainLayout
        title="Standard Crowdsale & Token"
        content={text}
        actionText="Deploy Token & Crowdsale"
        onAction={this.props.displayForm}
        error={!this.getContractAddress()}
      />
    );
  }

  renderFormLayout() {
    return (
      <StandardTokenStandardCrowdsaleFormLayout
        network={this.props.networkIdVerbose}
        walletAddress={this.props.walletAddress}
        handleContractExecute={this.handleContractExecute}
      />
    );
  }

  renderStatusLayout() {
    const {
      txHash,
      tokenAddress,
      crowdsaleAddress,
      executing,
      executingError,
      executeParams,
      networkId
    } = this.props;

    return (
      <StatusLayout
        networkId={networkId}
        executing={executing}
        executingError={executingError}
        txHash={txHash}
        tokenAddress={tokenAddress}
        crowdsaleAddress={crowdsaleAddress}
        executeParams={executeParams}
      />
    );
  }

  render() {
    const { display } = this.props;
    let leftPanel;

    switch (display) {
      case displayTypes.MAIN:
        leftPanel = this.renderMainLayout();
        break;
      case displayTypes.FORM:
        leftPanel = this.renderFormLayout();
        break;
      case displayTypes.STATUS:
        leftPanel = this.renderStatusLayout();
        break;
      default:
        leftPanel = null;
    }

    return (
      <div className="w-80 h-100 pv6 v-mid center">
        <div className="w-40-l dn dib-l fl pa5 center">
          <img className="w-100" src="/static/img/crowdsale.png" />
        </div>
        <div className="w-60-l fl ph4">{leftPanel}</div>
      </div>
    );
  }
}

const mapStateToProps = store => ({
  walletAddress: getWalletAddress(store),
  display: getDisplay(store),
  networkId: getNetworkId(store),
  networkIdVerbose: getNetworkIdVerbose(store),
  txHash: getTxHash(store),
  tokenAddress: getTokenAddress(store),
  crowdsaleAddress: getCrowdsaleAddress(store),
  executing: getExecuting(store),
  executingError: getExecutingError(store),
  executeParams: getExecuteParams(store)
});

const mapDispatchToProps = dispatch => ({
  displayForm: () => dispatch(displayForm()),
  loadWalletAddress: payload => dispatch(loadWalletAddress(payload)),
  contractExecute: payload => dispatch(contractExecute(payload))
});

StandardTokenStandardCrowdsaleContainer.propTypes = {
  loadWalletAddress: PropTypes.func,
  networkIdVerbose: PropTypes.string,
  contractExecute: PropTypes.func,
  walletAddress: PropTypes.string,
  txHash: PropTypes.string,
  tokenAddress: PropTypes.string,
  crowdsaleAddress: PropTypes.string,
  executing: PropTypes.bool,
  executingError: PropTypes.string,
  executeParams: PropTypes.object,
  networkId: PropTypes.number,
  display: PropTypes.string,
  displayForm: PropTypes.func
};

export default connect(mapStateToProps, mapDispatchToProps)(
  StandardTokenStandardCrowdsaleContainer
);



