export const initialState = {
  display: "MAIN",
  txHash: "",
  tokenAddress: "",
  executing: false,
  executingError: "",

  executeParams: {}
};

// Displays
export const displays = {
  MAIN: "MAIN",
  FORM: "FORM",
  STATUS: "STATUS"
};

// Actions
export const types = {
  DISPLAY_MAIN_STANDARD_TOKEN: "DISPLAY_MAIN_STANDARD_TOKEN",
  DISPLAY_FORM_STANDARD_TOKEN: "DISPLAY_FORM_STANDARD_TOKEN",
  DISPLAY_STATUS_STANDARD_TOKEN: "DISPLAY_STATUS_STANDARD_TOKEN",

  EXECUTE_STANDARD_TOKEN: "EXECUTE_STANDARD_TOKEN",
  EXECUTE_STANDARD_TOKEN_TX: "EXECUTE_STANDARD_TOKEN_TX",
  EXECUTE_STANDARD_TOKEN_SUCCESS: "EXECUTE_STANDARD_TOKEN_SUCCESS",
  EXECUTE_STANDARD_TOKEN_FAILURE: "EXECUTE_STANDARD_TOKEN_FAILURE"
};

// Reducers
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case types.DISPLAY_MAIN_STANDARD_TOKEN:
      return {
        ...state,
        display: displays.MAIN
      };
    case types.DISPLAY_FORM_STANDARD_TOKEN:
      return {
        ...state,
        display: displays.FORM
      };
    case types.DISPLAY_STATUS_STANDARD_TOKEN:
      return {
        ...state,
        display: displays.STATUS
      };
    case types.EXECUTE_STANDARD_TOKEN:
      return {
        ...state,
        executing: true,
        executeParams: action.payload
      };
    case types.EXECUTE_STANDARD_TOKEN_TX:
      return {
        ...state,
        display: displays.STATUS,
        txHash: action.payload
      };
    case types.EXECUTE_STANDARD_TOKEN_SUCCESS:
      return {
        ...state,
        tokenAddress: action.payload,
        executing: false
      };
    case types.EXECUTE_STANDARD_TOKEN_FAILURE:
      return {
        ...state,
        tokenAddress: "",
        executing: false,
        executingError: action.payload
      };
    default:
      return state;
  }
}

// Action Creators
export function contractExecute(payload) {
  return {
    type: types.EXECUTE_STANDARD_TOKEN,
    payload
  };
}

export function displayMain() {
  return {
    type: types.DISPLAY_MAIN_STANDARD_TOKEN
  };
}

export function displayForm() {
  return {
    type: types.DISPLAY_FORM_STANDARD_TOKEN
  };
}

export function displayStatus() {
  return {
    type: types.DISPLAY_STATUS_STANDARD_TOKEN
  };
}

// Selectors
export function getDisplay(store) {
  return store.standardToken.display;
}

export function getTxHash(store) {
  return store.standardToken.txHash;
}

export function getTokenAddress(store) {
  return store.standardToken.tokenAddress;
}

export function getExecuting(store) {
  return store.standardToken.executing;
}

export function getExecutingError(store) {
  return store.standardToken.executingError;
}

export function getExecuteParams(store) {
  return store.standardToken.executeParams;
}



