export const initialState = {
  display: "MAIN",
  txHash: "",
  tokenAddress: "",
  crowdsaleAddress: "",
  executing: false,
  executingError: "",

  executeParams: {}
};

// Displays
export const displays = {
  MAIN: "MAIN",
  FORM: "FORM",
  STATUS: "STATUS"
};

// Actions
export const types = {
  DISPLAY_MAIN_STANDARD_TOKEN_STANDARD_CROWDSALE:
    "DISPLAY_MAIN_STANDARD_TOKEN_STANDARD_CROWDSALE",
  DISPLAY_FORM_STANDARD_TOKEN_STANDARD_CROWDSALE:
    "DISPLAY_FORM_STANDARD_TOKEN_STANDARD_CROWDSALE",
  DISPLAY_STATUS_STANDARD_TOKEN_STANDARD_CROWDSALE:
    "DISPLAY_STATUS_STANDARD_TOKEN_STANDARD_CROWDSALE",

  EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE:
    "EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE",
  EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_TX:
    "EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_TX",
  EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_SUCCESS:
    "EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_SUCCESS",
  EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_FAILURE:
    "EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_FAILURE"
};

// Reducers
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case types.DISPLAY_MAIN_STANDARD_TOKEN_STANDARD_CROWDSALE:
      return {
        ...state,
        display: displays.MAIN
      };
    case types.DISPLAY_FORM_STANDARD_TOKEN_STANDARD_CROWDSALE:
      return {
        ...state,
        display: displays.FORM
      };
    case types.DISPLAY_STATUS_STANDARD_TOKEN_STANDARD_CROWDSALE:
      return {
        ...state,
        display: displays.STATUS
      };
    case types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE:
      return {
        ...state,
        executing: true,
        executeParams: action.payload
      };
    case types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_TX:
      return {
        ...state,
        display: displays.STATUS,
        txHash: action.payload
      };
    case types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_SUCCESS:
      return {
        ...state,
        tokenAddress: action.payload.tokenAddress,
        crowdsaleAddress: action.payload.crowdsaleAddress,
        executing: false
      };
    case types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_FAILURE:
      return {
        ...state,
        tokenAddress: "",
        crowdsaleAddress: "",
        executing: false,
        executingError: action.payload
      };
    default:
      return state;
  }
}

// Action Creators
export function contractExecute(payload) {
  return {
    type: types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE,
    payload
  };
}

export function displayMain() {
  return {
    type: types.DISPLAY_MAIN_STANDARD_TOKEN_STANDARD_CROWDSALE
  };
}

export function displayForm() {
  return {
    type: types.DISPLAY_FORM_STANDARD_TOKEN_STANDARD_CROWDSALE
  };
}

export function displayStatus() {
  return {
    type: types.DISPLAY_STATUS_STANDARD_TOKEN_STANDARD_CROWDSALE
  };
}

// Selectors
export function getDisplay(store) {
  return store.standardTokenStandardCrowdsale.display;
}

export function getTxHash(store) {
  return store.standardTokenStandardCrowdsale.txHash;
}

export function getTokenAddress(store) {
  return store.standardTokenStandardCrowdsale.tokenAddress;
}

export function getCrowdsaleAddress(store) {
  return store.standardTokenStandardCrowdsale.crowdsaleAddress;
}

export function getExecuting(store) {
  return store.standardTokenStandardCrowdsale.executing;
}

export function getExecutingError(store) {
  return store.standardTokenStandardCrowdsale.executingError;
}

export function getExecuteParams(store) {
  return store.standardTokenStandardCrowdsale.executeParams;
}



