import React from "react";
import PropTypes from "prop-types";

const openInNewWindow = link => {
  window.open(link, "_blank");
};

const renderStatus = ({ executingError, tokenAddress }) => {
  if (executingError)
    return <div className="f3 mb2 red">Failed: {executingError}</div>;
  if (tokenAddress)
    return <div className="f3 mb2 green">Successfully deployed!</div>;
  return <div className="f3 mb2 orange">Status: Waiting to be mined...</div>;
};

renderStatus.propTypes = {
  executingError: PropTypes.string,
  tokenAddress: PropTypes.string
};

const renderTxHash = ({ networkId, txHash }) => {
  if (!txHash) return null;
  let url;
  switch (networkId) {
    case 4:
      url = `https://rinkeby.etherscan.io/tx/${txHash}`;
      break;
    case 3:
      url = `https://ropsten.etherscan.io/tx/${txHash}`;
      break;
    case 42:
      url = `https://kovan.etherscan.io/tx/${txHash}`;
      break;
    default:
      url = "#";
  }
  return (
    <div>
      <div className="f4">Transaction Hash:</div>
      <div onClick={() => openInNewWindow(url)} className="pointer">
        {txHash}
      </div>
    </div>
  );
};

renderTxHash.propTypes = {
  networkId: PropTypes.number,
  txHash: PropTypes.string
};

const renderCrowdsaleSummary = ({ networkId, crowdsaleAddress }) => {
  if (!crowdsaleAddress) return null;

  let url;
  switch (networkId) {
    case 4:
      url = `https://rinkeby.etherscan.io/address/${crowdsaleAddress}`;
      break;
    case 3:
      url = `https://ropsten.etherscan.io/address/${crowdsaleAddress}`;
      break;
    case 42:
      url = `https://kovan.etherscan.io/address/${crowdsaleAddress}`;
      break;
    default:
      url = "#";
  }

  return (
    <div className="bg-near-white pa3 mt2">
      <div className="f3 mb3">Crowdsale</div>

      <div className="f5">Contract Address*:</div>
      <div className="pointer mb2" onClick={() => openInNewWindow(url)}>
        {crowdsaleAddress}
      </div>

      <div>*Buyers send ethers here to purchase the token.</div>
    </div>
  );
};

renderCrowdsaleSummary.propTypes = {
  networkId: PropTypes.number,
  crowdsaleAddress: PropTypes.string
};

const renderTokenSummary = ({ networkId, tokenAddress, executeParams }) => {
  if (!tokenAddress) return null;

  const { tokenSymbol, tokenDecimal } = executeParams;

  let url;
  switch (networkId) {
    case 4:
      url = `https://rinkeby.etherscan.io/token/${tokenAddress}`;
      break;
    case 3:
      url = `https://ropsten.etherscan.io/token/${tokenAddress}`;
      break;
    case 42:
      url = `https://kovan.etherscan.io/token/${tokenAddress}`;
      break;
    default:
      url = "#";
  }
  return (
    <div className="bg-near-white pa3 mt2">
      <div className="f3 mb3">Token Settings</div>

      <div className="f5">Contract Address:</div>
      <div className="pointer mb2" onClick={() => openInNewWindow(url)}>
        {tokenAddress}
      </div>

      <div className="f5">Token Symbol:</div>
      <div className="mb2">{tokenSymbol}</div>

      <div className="f5">Decimals:</div>
      <div className="mb2">{tokenDecimal}</div>
    </div>
  );
};

renderTokenSummary.propTypes = {
  networkId: PropTypes.number,
  tokenAddress: PropTypes.string,
  executeParams: PropTypes.object
};

const MainLayout = ({
  networkId,
  executingError,
  txHash,
  tokenAddress,
  crowdsaleAddress,
  executeParams
}) => (
  <div>
    <div className="f1">Deployment Status</div>
    {renderStatus({ executingError, tokenAddress })}
    {renderTxHash({ networkId, txHash })}
    {renderCrowdsaleSummary({ networkId, crowdsaleAddress })}
    {renderTokenSummary({ networkId, tokenAddress, executeParams })}
  </div>
);

MainLayout.propTypes = {
  executingError: PropTypes.string,
  networkId: PropTypes.number,
  txHash: PropTypes.string,
  tokenAddress: PropTypes.string,
  crowdsaleAddress: PropTypes.string,
  executeParams: PropTypes.object,
  fromAddress: PropTypes.string,
  tokenName: PropTypes.string
};

export default MainLayout;



