import _ from "lodash";
import { all, takeEvery, put, select } from "redux-saga/effects";
import { types } from "../reducers/standardTokenStandardCrowdsale";
import { getNetwork } from "../reducers/application";

import getWeb3 from "../services/web3/getWeb3";
import StandardTokenStandardCrowdsaleFactory from "../services/contracts/StandardTokenStandardCrowdsaleFactory.json";

// TODO do a better estimate
const DEFAULT_GAS = 2000000;

function* getSelectedWeb3() {
  const network = yield select(getNetwork);
  const web3 = yield getWeb3(network);
  return web3;
}

function txHashWrapper(tx) {
  return new Promise((resolve, reject) => {
    tx.on("transactionHash", resolve);
    tx.on("error", reject);
  });
}

export function* deployContract({ payload }) {
  try {
    const {
      factoryAddress,
      fromAddress,
      tokenName,
      tokenSymbol,
      tokenDecimal,
      tokenSupply,
      price
    } = payload;

    const web3 = yield getSelectedWeb3();

    const { abi } = StandardTokenStandardCrowdsaleFactory;
    const contract = new web3.eth.Contract(abi, factoryAddress, {
      from: fromAddress
    });

    const gasPrice = yield web3.eth.getGasPrice();
    const deployContractTransaction = contract.methods.deployContracts(
      tokenName,
      tokenSymbol,
      tokenDecimal,
      tokenSupply,
      fromAddress,
      price
    );

    const unresolvedTx = deployContractTransaction.send({
      from: fromAddress,
      gas: DEFAULT_GAS,
      value: web3.utils.toWei("0.05", "ether"),
      gasPrice
    });

    const txHash = yield txHashWrapper(unresolvedTx);

    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_TX,
      payload: txHash
    });

    const txReceipt = yield unresolvedTx;

    const deployedTokenAddress =
      _.get(txReceipt, "events.ContractsDeployed.returnValues.tokenAddress") ||
      _.get(txReceipt, "events.ContractsDeployed.returnValues.1") ||
      null;

    const deployedCrowdsaleAddress =
      _.get(
        txReceipt,
        "events.ContractsDeployed.returnValues.crowdsaleAddress"
      ) ||
      _.get(txReceipt, "events.ContractsDeployed.returnValues.2") ||
      null;

    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_SUCCESS,
      payload: {
        tokenAddress: deployedTokenAddress,
        crowdsaleAddress: deployedCrowdsaleAddress
      }
    });
  } catch (e) {
    yield put({
      type: types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE_FAILURE,
      payload: e.message
    });
  }
}

export default function* rootStandardTokenStandardCrowdsaleSaga() {
  yield all([
    takeEvery(types.EXECUTE_STANDARD_TOKEN_STANDARD_CROWDSALE, deployContract)
  ]);
}



